//  postDate  getResourse         ,            .

//Async_await     
//   postDate         await      
//   Async/await     res.json(),   fetch   ,        json
//async   
const postData = async (url, data) => { // Function expression. data  ,     .
    const res = await fetch(url, { //  ,        ,    await JS    .
        method: 'POST', //  
        headers: { //   formData, headers  
            'Content-type': 'application/json' //  ,  
        },
        body: data //   
    });

    return await res.json(); //  -,     json,                 await

};


const getResourse = async (url) => { // Function expression
    const res = await fetch(url);
    if (!res.ok) {
        throw new Error(`Could not fetch ${url}, status: ${res.status}`); //          throw
    }
    return await res.json(); //  ,     json
};

export {postData};

export {getResourse};