//closeModal  openModal   modal,          (forms.js).
function closeModal(modalSelector) {
    const modal = document.querySelector(modalSelector);
    modal.classList.add('hide');
    modal.classList.remove('show');
    document.body.style.overflow = '';
}

function openModal(modalSelector, modalTimerId) {
    const modal = document.querySelector(modalSelector);
    modal.classList.add('show');
    modal.classList.remove('hide');
    document.body.style.overflow = 'hidden';

    console.log(modalTimerId);
    if (modalTimerId) { // clearInterval     modalTimerId
        clearInterval(modalTimerId);
    }
    
}

function modal(triggerSelector, modalSelector, modalTimerId) {
        //Modal
    /*         HTML  /     ,
            ,       ,    data 
        <button data-modal class="btn btn_white">  </button>
          data     
         <div data-close class="modal__close">&times;</div> */

    //       "  "
    const modalTrigger = document.querySelectorAll(triggerSelector), //    .  .
        modal = document.querySelector(modalSelector); //     
    //  .    054,        ,
    //      JS.   modalCloseBtn
    //  modalCloseBtn = document.querySelector('[data-close]'); //    



    /*    modalTrigger.addEventListener('click', () => { //    "  "   
           modal.classList.add('show');
           modal.classList.remove('hide');
           document.body.style.overflow = 'hidden'; //      
          });

       modalCloseBtn.addEventListener('click', () => { // ,       ,  
           modal.classList.add('hide'); //  
           modal.classList.remove('show'); //  
           document.body.style.overflow = ''; //   ,   
       });   */


    modalTrigger.forEach(btn => {
        btn.addEventListener('click', () => openModal(modalSelector, modalTimerId));
    }); //    ,  openModal(modalSelector)  ,    . 

  

    /* 
        //     ,    :
        modalTrigger.forEach(btn => { //           
            //      ,       
            btn.addEventListener('click', openModal); //    "  "   
        });

        //         ,       
        function openModal() {
            modal.classList.toggle('show'); //  show (   ,     )
            document.body.style.overflow = 'hidden'; //      
            clearInterval(modalTimerId); //   ,       .
        }

        function closeModal() { //   
            modal.classList.toggle('show'); //     ,       .
            document.body.style.overflow = ''; //   ,   
        }
     */
    //  054   ,       
    //modalCloseBtn.addEventListener('click', closeModal); // ,       ,  

    modal.addEventListener('click', (e) => { //e.target  ,   
        // 054   ,    :
        //e.target.getAttribute('data-close')
        if (e.target === modal || e.target.getAttribute('data-close') == '') { //    (modal)   
            //e.target === modal -   
            //e.target.getAttribute('data-close') -     
            closeModal(modalSelector); //      ,   ,  
        }
    });

    document.addEventListener('keydown', (e) => { //    ESC   
        //      https://keycode.info/
        if (e.code === "Escape" && modal.classList.contains('show')) { //      ESC     ,       
            closeModal(modalSelector);
        }
    });

    //044   . 
    //           

    

    function showModalByScroll() {
        if (window.pageYOffset + document.documentElement.clientHeight >= document.documentElement.scrollHeight) {
            //  ,  ,     .
            // pageYOffset   px  -   Y ( )
            // scrollHeight          
            //clientHeight  ,    
            openModal(modalSelector, modalTimerId);
            window.removeEventListener('scroll', showModalByScroll); //  -      ,     ,    .
        }
    }
    window.addEventListener('scroll', showModalByScroll); //       
}

export default modal;
export {closeModal};
export {openModal};