import {closeModal, openModal} from './modal';
import {postData} from '../services/services';
function forms(formSelector, modalTimerId) {
    
    //053 Forms.          XMLHttpRequest
    //    backEnd - server.php
    const forms = document.querySelectorAll(formSelector); //     "form"
    const message = { //     - 
        //loading: '',
        loading: 'img/form/spinner.svg',
        success: '!     ',
        failure: '    ...'
    };

    forms.forEach(item => { //     bindPostData
        bindPostData(item);
    });

    //059    . Async_await

    //Async_await     
    //   postData         await      
    //   Async/await     res.json(),   fetch   ,        json
    //async   

    //073. postData     services,            

    function bindPostData(form) { //  
        form.addEventListener('submit', (e) => { // submit        
            //            btn 
            e.preventDefault(); //        

            let statusMessage = document.createElement('img'); //   
            statusMessage.src = message.loading;
            //statusMessage.textContent = message.loading; //      
            statusMessage.style.cssText = `
        display: block;
        margin: 0 auto;
        `;
            //form.append(statusMessage); //    
            form.insertAdjacentElement('afterend', statusMessage);

            /*    Fetch (056)
            const request = new XMLHttpRequest(); //  XMLHttpRequest
            request.open('POST', 'server.php'); //  open    */


            //       :
            //1) formData
            //2) JSON
            //   (formData+XMLHttpRequest),      !!!
            //request.setRequestHeader('Content-type', 'multipart/form-data');
            //2)  JSON   request.setRequestHeader('Content-type', 'application/json');

            //request.setRequestHeader('Content-type', 'application/json'); 056 Fetch
            //request.setRequestHeader(); // Fetch   

            const formData = new FormData(form); //formData
            //       formData, 
            //         name

            //  formData   JSON    059,  .
            //formData             
            /*  const object = {};
             formData.forEach(function (value, key) { // formData     object
                 object[key] = value;
             }); */
            //      (object),   formData
            //const json = JSON.stringify(object); //   JSON 

            const json = JSON.stringify(Object.fromEntries(formData.entries()));
            //formData     ,      ,      ,        json.
            //entries           


            //request.send(formData); //   1)formData
            //    Fetch 056: 
            //request.send(json); //2)  JSON

            postData('http://localhost:3000/requests', json)
                //.then(data => data.text()) //      .    059
                .then(data => {
                    console.log(data); //   ,   
                    showThanksModal(message.success); //  
                    statusMessage.remove();
                }).catch(() => { //    ,   
                    showThanksModal(message.failure);
                }).finally(() => { //     
                    form.reset(); // 
                });

            /*        Fetch 056
            request.addEventListener('load', () => {  //load -   
                    if (request.status === 200) {
                        console.log(request.response);
                        showThanksModal(message.success); //  
                         //setTimeout(() => { //     2 . 
                        statusMessage.remove();
                        form.reset(); // 
                        //}, 2000); 
                    } else {
                        showThanksModal(message.failure); //  
                    }
                });  */
        });
    }

    //054.  .
    //           
    function showThanksModal(message) {
        const prevModalDialog = document.querySelector('.modal__dialog'); //  

        prevModalDialog.classList.add('hide'); //    
        openModal('.modal', modalTimerId); //  

        const thanksModal = document.createElement('div');
        thanksModal.classList.add('modal__dialog'); //   ,    
        //   
        thanksModal.innerHTML = `
      <div class="modal__content">
        <div class="modal__close" data-close></div>
        <div class="modal__title">${message}</div>
      </div>
    `;
        //  modalCloseBtn     modalCloseBtn.addEventListener('click', closeModal);
        document.querySelector('.modal').append(thanksModal);
        setTimeout(() => {
            thanksModal.remove(); //   
            prevModalDialog.classList.add('show');
            prevModalDialog.classList.remove('hide');
            closeModal('.modal');
        }, 1000);

    }

    /*   //056. Fetch API (application programming interface)   
      Fetch   XMLHttpRequest
       -             .
        API  DOM API,     (  querySelector),        
            Fetch.
      //Fetch     ,   
      https://jsonplaceholder.typicode.com/    

          // GET 
          fetch('https://jsonplaceholder.typicode.com/todos/1') // 
          .then(response => response.json()) //response - .
          //response.json()  json     JS .     ,        json     ,     .    ,    then.
          .then(json => console.log(json));

      //   2    :
      //   - XMLHttpRequest(XHR)
      //   - Fetch

      //POST 
      fetch('https://jsonplaceholder.typicode.com/posts', { //     .  Fetch   url   .     ,     GET .
              method: 'POST',
              body: JSON.stringify({
                  name: 'Alex'
              }), //   , JSON.stringify      JSON 
              headers: { //
                  'Content-type': 'application/json'
              }
          })
          .then(response => response.json())
          .then(json => console.log(json));


      PUT   
      PATCH   */

    //058. npm. JSON server
    fetch('http://localhost:3000/menu') //    json-server db.json     
        .then(data => data.json())
        .then(res => console.log(res));
}

export default forms;