function tabs(tabsSelector, tabsContentSelector, tabsParentSelector, activeClass) {
    //Tabs
    /*  3 :
     1)      
     2)   
     3)     */
    const tabs = document.querySelectorAll(tabsSelector), //      ,    
        tabsContent = document.querySelectorAll(tabsContentSelector), //    
        tabsParent = document.querySelector(tabsParentSelector); //     /

    /*         inline ,    .  CSS   ,       ,     inline 
    .show{display:block}.hide{display:none}.fade{animation-name: fade;animation-duration: 1.5s;}@keyframes fade{from{opacity: 0.1;}to{opacity: 1;}}
     show   , hide  , fade    (),  1,5 ,   10%  100% */

    function hideTabContent() { //    
        tabsContent.forEach(item => { // 
            // item.style.display = 'none';  inline 
            item.classList.add('hide'); // inline   .   hide
            item.classList.remove('show', 'fade'); //  show  
        });
        tabs.forEach(item => {
            item.classList.remove(activeClass); //      ,     ,  classList,    .
        });
    }

    function showTabContent(i = 0) { //    ,    0,    .
        //tabsContent[i].style.display = 'block';  inline ,      .
        tabsContent[i].classList.add('show', 'fade'); // inline   .   show  
        tabsContent[i].classList.remove('hide');
        tabs[i].classList.add(activeClass);
    }

    hideTabContent();
    showTabContent();

    //  
    tabsParent.addEventListener('click', (event) => {
        const target = event.target; //,   ,     event.target
        //073.  classList    ,    
        if (target && target.classList.contains(tabsSelector.slice(1))) { 
            tabs.forEach((item, i) => {
                /*          tabs   
                                       ,          
                           item -  , i -      .
                           target          */
                if (target == item) { //             
                    hideTabContent(); //  
                    showTabContent(i); //  
                }
            });
        }
    });

}

export default tabs;